
import React, {useState, useEffect} from 'react'
import axios from 'axios'
import personService from './services/persons'


const Notification = ({message}) =>{
  if(message === null){
    return null
  }

  return(
    <div className = "message">
      {message}
    </div>
  )
}

const NotificationFailed = ({message}) =>{
  if(message === null){
    return null
  }

  return(
    <div className = "messageError">
      {message}
    </div>
  )
}

const Name = (props) =>{
    const namePerson = props.person
    return(
      <div>
        {namePerson["name"]} {namePerson["number"]} <button onClick = {props.buttonHandle}>delete</button>
      </div>
    )
  }
  
  const NameTotal = (props)=>{
    const numbersArray = props.numbers  
    const onDeleteClick = (id)=>{
      const url = `http://localhost:3001/persons/${id}`
      const person = numbersArray.find( p => p.id === id)
      const proceedDelete = window.confirm(
        `Delete ${person["name"]}?`
      )
      if(proceedDelete){
        axios.delete(url)
        .then( () =>{
          props.setPersons(numbersArray.filter(p => p.id !== id))
          props.setMessage(
            `Successfully removed ${person["name"]}`
          )
          setTimeout(()=>{
            props.setMessage(null)
          },3000)
        })
        .catch(error=>{
          props.setMessageError(
            `Failed ${person["name"]} not found`
          )
          setTimeout(()=>{
            props.setMessageError(null)
            props.setPersons(numbersArray.filter(p=> p.id !== id))
          },3000)

        })
      }

    }
    return(
      <div>
        {numbersArray.map((nameObject) => {
          return <Name person = {nameObject} key = {nameObject["name"]} buttonHandle = {() => onDeleteClick(nameObject.id)} />
        })}
      </div>
    )
  }
  
  const Filter = (props)=>{
    return(
      <div>
      filter shown with <input
            value = {props.newFilter}
            onChange = {props.handleFilterChange}/>
    </div>
    )
  }
  
  const PersonForm = (props) =>{
    return(
      <form onSubmit = {props.addName}>
          <div>
            <div> name: <input
                value = {props.newName}
                onChange = {props.handleNameChange}/>
            </div>
            <div>
                number: <input
                value = {props.newNumber}
                onChange = {props.handleNumberChange}/>
            </div>
          </div>
          <div>
            <button type="submit">add</button>
          </div>
        </form>
    )
  }
  
  
  
  const App = () =>{
    const[persons,setPersons] = useState([])
  
    const [newName,setNewName] = useState('')
  
    const[newNumber, setNewNumber] = useState('')
  
    const [newFilter,setNewFilter] = useState('')

    const[message, setMessage] = useState(null)

    const [messageError, setMessageError] = useState(null)


    useEffect(()=>{
      personService
          .getAll()
          .then(initialPeople =>{
            setPersons(initialPeople)
          })
          .catch(error =>{
            setMessageError(
              `Failed. Resources not found`
            )

            setTimeout(()=>{
              setMessageError(null)
            },3000)
          })

    },[])
   
    const addName = (event) =>{
      event.preventDefault()
      var varResult = persons.find((person) => person["name"] === newName)
  
      if(typeof varResult !== 'undefined'){
        const id = varResult.id
        const url = `http://localhost:3001/api/persons/${id}`
        const proceedUpdate = window.confirm(`${newName} replace the old number with a new number ?`)
        if(proceedUpdate){
          const changedPerson = {
            name: newName,
            number: newNumber
          }

          personService.update(id, changedPerson)
          .then(returnedPerson=>{
            setPersons(persons.map(p => p.id !== id ? p : returnedPerson))
            setMessage(
              `Successfully updated ${returnedPerson["name"]}`
            )

            setTimeout(()=>{
              setMessage(null)
            }, 3000)
          })
          .catch(error =>{
            setMessageError(
              `Failed ${changedPerson["name"]} not found`
            )

            setTimeout(() =>{
              setMessageError(null)
            },3000)
          })

        }
        setNewName('')
        setNewNumber('')
  
      }
      else{
        const newPerson = {
          name : newName,
          number : newNumber
        }

        personService.create(newPerson)
                      .then(returnedPerson=>{
                        setPersons(persons.concat(returnedPerson))
                        setNewName('')
                        setNewNumber('')
                        setMessage(
                          `Successfully added ${newPerson["name"]}`
                        )

                        setTimeout(()=>{
                          setMessage(null)
                        },3000)

                      })
                      .catch(error =>{
                        setNewName('')
                        setNewNumber('')
                        setMessageError(
                          `Failed ${newPerson["name"]} not found`

                        )
                        setTimeout(()=>{
                          setMessage(null)
                        },3000)
                      })      
      }
    }
  
    const handleNameChange = (event) =>{
      setNewName(event.target.value)
    }
  
    const handleNumberChange = (event) =>{
      setNewNumber(event.target.value)
    }
  
    const handleFilterChange = (event) =>{
      setNewFilter(event.target.value)
    }
  
    const namesToShow = persons.filter((person)=> {
      let personName = person["name"].toLowerCase()
      let filterUsed = newFilter.toLowerCase()
      return personName.includes(filterUsed)
    })
  
    return (
      <div>
        <Notification message = {message} />
        <NotificationFailed message = {messageError}/>
        <h2>Phonebook</h2>
       <Filter newFilter = {newFilter} handleFilterChange = {handleFilterChange}/>
        <h2>add a new</h2>
        <PersonForm addName = {addName} newName = {newName}
        handleNameChange = {handleNameChange}
        newNumber = {newNumber} 
        handleNumberChange = {handleNumberChange}/>
        <h2>Numbers</h2>
    <NameTotal numbers = {namesToShow} setPersons = {setPersons} 
    message = {message} setMessage = {setMessage}
    messageError = {messageError} setMessageError = {setMessageError}/>
      </div>
    )
  }

  export default App